/* Home.css */

.container{
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader{
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.movies{
  display: grid;
  /* repeat(반복해라)(2(열의 갯순), minmax(최소크기, 최대크기)) / 1fr : 1대1 비율*/
  grid-template-columns: repeat(2, minmax(400px, 1fr));
  grid-gap: 100px;
  padding: 50px;
  width: 80%;
  padding-top: 70px;
}

@media screen and (max-width: 767px) { 
  /* 0 ~ 767 */
  .movies{
    grid-template-columns: 1fr;
    width: 100%;
  }
}